    @action
    findDogsByTeam(teamId) {
        if (Array.isArray(this.dogs)) {
            const dogs = this.dogs.filter((e) => {
                if (e.team === teamId) {
                    return true;
                }
                if (e.teams && e.teams.indexOf(teamId) > -1) {
                    return true;
                }
                return false;
            });
            return dogs;
        }
        return null;
    }

