import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';
import util from 'preact-util';
import { route } from 'preact-router';

const FONTSIZE_MENU = '12px;';
const FONTSIZE_ICON = '23px;';
const FONTSIZE_LARGE_ICON = '40px;';


const initialState = {
    menu: {
		profile: false,
		main: false,
		showDashoard: false,
	},
	searchText: '',
};

@observer
class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.apiServer = util.getApiServer();
    }

	toggleDashboard = () => {
		const { appState } = this.props.stores;
		appState.toggleDashboard();
	}

	search = () => {
		const { searchText } = this.state;
		this.toggleDashboard();
		route(`/search/${searchText}`);
	}

    render() {
		const { searchText } = this.state;
		const { appState, userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		const { user = {}, isAdmin, isTester, isVeterinary } = userStore;
		const { path, isCordova, apiServer, apiServerChanged, showDashoard } = appState;

		let pathFixed = path.replace(/^\//, '');
		pathFixed = pathFixed.replace(/\/$/, '');
		pathFixed = pathFixed.replace(/\w{8,}-\w{4,}-\w{3,}-\w{3,}-\w{12,}/, 'uuid');
		pathFixed = pathFixed.replace(/\d+/g, 'id');
		// 8086094d-fbc3-44af-acc0-7c1ae9b5dce7
		pathFixed = pathFixed.replace(/\/$/, '');
		pathFixed = pathFixed.replace(/\//g, '.');
		if (pathFixed === '') {
			pathFixed = 'frontpage';
		}
		

		return (<>
			{showDashoard && <div class='container-fluid border-bottom border-top bg-light fixed-bottom' style='z-index: 99999;'>
				<button 
					class='btn btn-lg btn-danger position-absolute' 
					style='top: 5px; right: 5px; z-index: 10000;'
					type='button' 
					onClick={this.toggleDashboard}
				>
					<i class='fas fa-times' />
				</button>
				<div class='row'>
					<div class='col-12 text-center py-3'>
						<h5><Text id='footer.dashboard'>Dashboard</Text></h5>
					</div>

					<div class={`col-6 col-sm-4 p-4 text-center p-2 ${path.match(/\/dogs/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/dogs/' class={`text-${path.match(/\/dogs/) ? 'primary' : 'muted'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-dog' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='footer.dogs'>Hundene</Text>
						</a>
					</div>

					<div class={`col-6 col-sm-4 p-4 text-center p-2 ${path.match(/\/tracks/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/tracks/' class={`text-${path.match(/\/tracks/) ? 'primary' : 'muted'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-map-marked-alt' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='footer.tracks'>Rundene</Text>
						</a>
					</div>

					<div class={`col-6 col-sm-4 p-4 text-center p-2 ${path.match(/\/users\//) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/users/' class={`text-${path.match(/\/users\//) ? 'primary' : 'muted'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-user' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='header.profile'>Din profil</Text>
						</a>
					</div>

					<div class={`col-6 col-sm-4 p-4 text-center p-2 ${path.match(/^\/settings/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/settings/' class={`text-${path.match(/^\/settings/) ? 'primary' : 'success'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-cog' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='users.settings'>Settings</Text>
						</a>
					</div>

					<div class={`col-6 col-sm-4 p-4 text-center p-2 ${path.match(/\/teams\/connect/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/teams/connect' class={`text-${path.match(/\/teams\/connect/) ? 'primary' : 'muted'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-project-diagram' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='header.connect-team'>Follow teams</Text>
						</a>
					</div>

					<div class={`col-6 col-sm-4 p-4 text-center p-2 ${path.match(/\/support/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='https://themusher.no/v2/support/faq-frequently-asked-questions/13' data-native class={`text-${path.match(/\/support/) ? 'primary' : 'muted'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-question-circle' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='header.support'>Support</Text>
						</a>
					</div>
				</div>
				<div class='row'>
					<div class={`col-6 col-sm-4 p-4 text-center p-2 ${path.match(/\/app-races/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/app-races/list' class={`text-${path.match(/\/app-races/) ? 'primary' : 'muted'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-flag-checkered' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='header.races'>Races</Text>
						</a>
					</div>

					{isVeterinary && <div class={`col-6 col-sm-4 p-4 text-center text-primary p-2 ${path.match(/\/veterinary/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/veterinary/' class={`text-${path.match(/\/veterinary/) ? 'primary' : 'info'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-syringe' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='footer.veterenary'>Vet</Text>
						</a>
					</div>}

					{isAdmin && <div class={`col-6 col-sm-4 p-4 text-center text-primary p-2 ${path.match(/\/admin/) ? '' : ''} text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
						<a href='/admin/' class={`text-${path.match(/\/admin/) ? 'primary' : 'danger'}`} onClick={this.toggleDashboard}>
							<i class='fas fa-user-shield' style={`font-size: ${FONTSIZE_LARGE_ICON};`} /><br />
							<Text id='footer.admin'>Admin</Text>
						</a>
					</div>}

					{user && user.id && path && <img src={`${this.apiServer}/api/pageview/dashboard/${user.id}/${currentTeam}/${new Date().getTime()}`} style='width: 1; height: 1; opacity: 0.1;' />}
				</div>
				<div class='row'>
					<div class='col-12 d-flex justify-content-center py-3'>
						<Localizer>
							<input
								class='form-control form-control-lg'
								type='search' 
								placeholder={<Text id='header.search-placeholder'>Søk etter det meste...</Text>}
								value={searchText}
								onInput={linkState(this, 'searchText')}
							/>
						</Localizer>
						<button
							class='btn btn-lg btn-outline-success'
							type='button'
							onClick={this.search}
						><Text id='header.search'>Søk</Text></button>
					</div>
				</div>
			</div>}

			<div
				class='navbar-themed fixed-bottom bg-light pb-2'
				style={`
					font-size: ${FONTSIZE_MENU};
				`}
			>
				{/* {isVeterinary && <>
					<div class='bg-success'>
						<small>
						<small class='text-white font-weight-bold'>
							Approved veterinary
						</small>
						</small>
					</div>
				</>} */}



				<div class='container-fluid'>
					<div class='row pb-1'>
						<div class={`col text-center p-2 ${path.match(/(^\/$|\/home-wall)/) ? 'border-bottom border-primary' : ''} text-nowrap overflow-hidden`} style='text-overflow: ellipsis;'>
							<a href={`${path === '/' ? '/home-wall' : '/'}`} class={`text-${path.match(/(^\/$|\/home-wall)/) ? 'primary' : 'secondary'}`}>
								<i class='fas fa-home' style={`font-size: ${FONTSIZE_ICON};`} /><br />
								<Text id='footer.home'>Home</Text>
							</a>
						</div>
						<div class={`col text-center p-2 ${path.match(/\/workouts/) ? 'border-bottom border-primary' : ''} text-nowrap overflow-hidden`} style='text-overflow: ellipsis;'>
							<a href='/workouts/' class={`text-${path.match(/\/workouts/) ? 'primary' : 'secondary'}`}>
								<i class='fas fa-running' style={`font-size: ${FONTSIZE_ICON};`} /><br />
								<Text id='footer.workouts'>Treningen</Text>
							</a>
						</div>

						{isCordova && <div class={`col text-center p-2 ${path.match(/\/workouts\/tracking/) ? 'border-bottom border-primary' : ''} text-nowrap overflow-hidden`} style='text-overflow: ellipsis;'>
							<a href='/workouts/tracking/' class={`text-${path.match(/\/workouts\/tracking/) ? 'primary' : 'danger'}`}>
								<i class='far fa-dot-circle' style={`font-size: ${FONTSIZE_ICON};`} /><br />
								<Text id='footer.workouts-start'>Start</Text>
							</a>
						</div>}

						<div class={`col text-center p-2 ${path.match(/\/teams/) ? 'border-bottom border-primary' : ''} text-nowrap overflow-hidden`} style='text-overflow: ellipsis;'>
							<a href='/teams/' class={`text-${path.match(/\/teams/) ? 'primary' : 'secondary'}`}>
								<i class='fas fa-users' style={`font-size: ${FONTSIZE_ICON};`} /><br />
								<Text id='footer.teams'>Teamene</Text>
							</a>
						</div>
						<div class={`col text-center p-2 text-nowrap overflow-hidden d-flex align-items-center justify-content-center`} style='text-overflow: ellipsis;'>
							<span class={`text-${showDashoard ? 'primary' : 'secondary'}`} onClick={this.toggleDashboard}>
								<i class='fas fa-ellipsis-h' style={`font-size: ${FONTSIZE_ICON};`} /><br />
								<Text id='footer.menu'>Menu</Text>
							</span>

							{user && user.id && path && <img src={`${this.apiServer}/api/pageview/${pathFixed}/${user.id}/${currentTeam}/${new Date().getTime()}`} style='width: 1; height: 1; opacity: 0.1;' />}
						</div>
					</div>
				</div>

				{isAdmin && apiServerChanged && <div class='position-absolute badge badge-primary' style='top: 45px; right: 0;'>
					<span class='font-weight-lighter'>apiServer:</span> {apiServer}
				</div>}


			</div>
		</>);
	}
}

export default Navigation;
