import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';

configure({ enforceActions: 'always' });

class DogJournalStore extends StoreModel {
    constructor() {
        super('dogJournal', {
            namePlural: 'dogJournals',
            sort: 'title',
            limit: 100,
            api: {
                search: {
                    url: '/api/veterinary/dog-journal/',
                    params: {
                        extendedView: 1,
                        limit: 15,
                        sort: 'id',
                    },
                },
                load: {
                    url: '/api/veterinary/dog-journal/',
                    params: {},
                },
                save: {
                    url: '/api/veterinary/dog-journal/',
                    params: {},
                },
            },
        });
    }

    @observable dogJournal = {};

    @observable dogJournals = [];
}

const store = new DogJournalStore();
export default store;
